<template>
  <div class="container">
    <!---->
      <div class="container-box" v-if="loginShow">
          <h3>
            
          </h3>
        <el-form  :model="loginForm" :rules="loginRules" ref="ruleLogin">
          <el-form-item  class="login-input" prop="uname">
            <el-input  placeholder="" v-model="loginForm.uname"></el-input>
          </el-form-item>
          <el-form-item class="login-input" prop="pass">
            <el-input placeholder="" type="password" v-model="loginForm.pass" ></el-input>
          </el-form-item>
          <el-form-item class="login-input login-btn">
            <div class="container-box-forget">
              <span @click = "toForgot('ruleForgot')">
                    
              </span>
            </div>
            <el-button @click="handlerLogin" :loading="loginLoading"></el-button>
            <div class="container-box-reg-text">
                <span @click="toReg('ruleReg')">
                  
                </span>
            </div>
          </el-form-item>
        </el-form>
      </div>
    <!---->
      <div class="container-reg" v-if="regShow">
        <h3>
          
        </h3>
        <el-form  :model="regForm" :rules="regRules" ref="ruleReg">
          <el-form-item  class="login-input" prop="mobile">
            <el-input  placeholder="" v-model="regForm.mobile"></el-input>
          </el-form-item>
          <el-form-item class="login-input reg-code" prop="code">
            <el-input placeholder=""  v-model="regForm.code" ></el-input>
            <div class="el-btn">
              <el-button :loading="smsLoading" @click="postCode">{{smsLoading?smsSec+'s':''}}</el-button>
            </div>
          </el-form-item>
          <el-form-item class="login-input reg-code" prop="code" v-show="false">
            <el-input placeholder=""  v-model="regForm.code" ></el-input>
            <div class="el-btn">
              <el-button :loading="smsLoading" @click="postCode">{{smsLoading?smsSec+'s':''}}</el-button>
            </div>
          </el-form-item>
          <el-form-item class="login-input" prop="pass2">
            <el-input placeholder="" type="password" v-model="regForm.pass2" ></el-input>
          </el-form-item>


          <el-form-item class="login-input" prop="name">
            <el-input placeholder=""  v-model="regForm.name" ></el-input>
          </el-form-item>

          <el-form-item class="login-btn login-input" prop="radio">
            <div class="container-reg-info">
              <div >
              </div>
              <div >
                <span>
                   <el-radio v-model="regForm.radio" label="1"></el-radio>
              </span>
              </div>
            </div>
            <el-button @click="handlerReg('ruleReg')"></el-button>
            <div class="container-box-forget">
                <span @click="toLogin('ruleLogin')">
                  
                </span>
            </div>
          </el-form-item>
        </el-form>
      </div>
    <!---->
    <div class="container-reg" v-if="forgetShow" >
      <h3>
        
      </h3>
      <el-form  :model="ForgotForm" :rules="forgetRules" ref="ruleForgot">
        <el-form-item  class="login-input" prop="mobile">
          <el-input  placeholder="" v-model="ForgotForm.mobile"></el-input>
        </el-form-item>
        <el-form-item class="login-input reg-code" prop="code">
          <el-input placeholder=""  v-model="ForgotForm.code" ></el-input>
          <div class="el-btn">
            <el-button :loading="smsLoading" @click="postCode">{{smsLoading?smsSec+'s':''}}</el-button>
          </div>
        </el-form-item>
        <el-form-item class="login-input reg-code" prop="code" v-show="false">
          <el-input placeholder=""  v-model="ForgotForm.code" ></el-input>
          <div class="el-btn">
            <el-button :loading="smsLoading" @click="postCode">{{smsLoading?smsSec+'s':''}}</el-button>
          </div>
        </el-form-item>
        <el-form-item class="login-input" prop="pass3">
          <el-input placeholder="" type="password" v-model="ForgotForm.pass3" ></el-input>
        </el-form-item>
        <el-form-item class="login-input" prop="repass">
          <el-input placeholder=""  v-model="ForgotForm.repass" ></el-input>
        </el-form-item>

        <el-form-item class="login-btn login-input">
          <div class="container-reg-info">
            <div >
            </div>
            <div >
                <span>
                   <el-radio v-model="ForgotForm.radio" ></el-radio>
              </span>
            </div>

          </div>
          <el-button @click="handlerForgot"></el-button>
          <div class="container-box-forget">
                <span @click="toLogin('ruleLogin')">
                  
                </span>
          </div>
        </el-form-item>
      </el-form>
    </div>
    <!---->
    <div class="container-agreement" v-if ='agreementShow' >
      <span @click="closeAgreement" class="el-icon-close">
      </span>
      <div class="container-agreement-box">
        <h3>
            
        </h3>
        <div >
         <p>
           ""
         </p>
          <p>
            
          </p>

          <p>
            
          </p>
          <p>          
          </p>
          <p>
            

          </p>
          <p>
            

          </p>
          <p>
            

          </p>
          <p>
            

          </p>
          <p>
            

          </p>
          <p>
            

          </p>
          <p>
            

          </p>

          <p>
            
          </p>
          <p>
            
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  const postCodeUrl= 'regsendmsg'
  const regisUrl ='regis'
  const postLogin='login'

    export default {
      name: "index",
      data(){
          return {
            loginLoading:false,
            showAskStatement:'',
            smsSec:0,
            smsLoading:false,
            loginShow:true,
            regShow:false,
            forgetShow:false,
            agreementShow:false,
            loginForm:{
              uname:'',
              pass:''
            },
            regForm:{
              radio:'0',
              mobile:'',
              code:'',
              pass2:'',
              name:''
            },
            ForgotForm:{
              radio:true,
              mobile:'',
              code:'',
              pass:'',
              repass:''
            },
            loginRules: {
              uname: [
                {required: true, message: '', trigger: 'change'},
                {min: 11, max: 11, message: '11', trigger: 'change'}
              ],
              pass:[
                {required: true, message: '', trigger: 'change'},
                {min: 6, max: 8, message: '6-8', trigger: 'change'}
              ]
            },
            regRules:{
              mobile: [
                {required: true, message: '', trigger: 'change'},
                {min: 11, max: 11, message: '11', trigger: 'change'}
              ],
              code: [
                {required: true, message: '', trigger: 'change'},
                {min:4, max: 4, message: '4', trigger: 'change'}
              ],
              pass2:[
                {required: true, message: '', trigger: 'change'},
                {min: 6, max: 8, message: '6-8', trigger: 'change'}
              ],
              name:[
                {required: true, message: '', trigger: 'change'},
                {min: 6, max: 8, message: '6-8', trigger: 'change'}
              ],
              radio: [
                { required: true, message: '', trigger: 'change' }
              ],
            },
            forgetRules:{
              mobile: [
                {required: true, message: '', trigger: 'change'},
                {min: 11, max: 11, message: '11', trigger: 'change'}
              ],
              code: [
                {required: true, message: '', trigger: 'change'},
                {min:4, max: 4, message: '4', trigger: 'change'}
              ],
              pass3:[
                {required: true, message: '', trigger: 'change'},
                {min: 6, max: 8, message: '6-8', trigger: 'change'}
              ],
              repass:[
                {required: true, message: '', trigger: 'change'},
                {min: 6, max: 8, message: '6-8', trigger: 'change'}
              ]
            }
          }
      },
      methods:{
        //
        closeAgreement(){
          this.agreementShow=false
          this.regShow=true
        },
        //
        toForgot(val){
          this.loginShow=false;
          this.forgetShow=true

          this.$nextTick(()=>{
            this.$refs[val].resetFields();
          })

        },
        //
        toReg(val){
          this.loginShow=false;
          this.regShow=true
          this.$nextTick(()=>{
            this.$refs[val].resetFields();
          })

        },
        //
        toLogin(val){
          this.forgetShow=false
          this.regShow=false
          this.loginShow=true;
          this.$nextTick(()=>{
            this.$refs[val].resetFields();
          })

        },
        //
        async postCode(){
          if(this.regForm.mobile.length===11){
           this.smsLoading =true;
           this.smsSec=3;
            let timer = setInterval(()=>{
              if(this.smsSec>0){
                this.smsSec--;

              }else {
                this.smsSec=0
                this.smsLoading =false
                clearInterval (timer)
              }
            },1000);
           let res =await this.$ajax.post(postCodeUrl,{
             mobile:this.regForm.mobile,
             type:'pc'
           });
            if (res.data.state){
              this.$notify.success({
                title: '',
                message: ''
              });
            }else {
              this.$notify.error({
                title: '',
                message: ''
              });
            }


          }else {
            this.$notify.error({
              title: '',
              message: ''
            });
          }
        },
        // 
        handlerLogin(){
          this.$refs['ruleLogin'].validate((valid) => {
            if (valid) {
              this.loginLoading=true
              this.$ajax.post(postLogin,{
                mobile:this.loginForm.uname,
                password:this.loginForm.pass,
                type:'pc'
              }).then(res=>{

                if(res.data.state){
                  //todo 
                 this.$cookie.set('token',res.data.data.common.app.skey,res.data.data.common.app.expire)
                  this.$notify.success({
                    title: '',
                    message: ''
                  });
                  this.loginLoading=true
                    if(window.localStorage.getItem('showAgreement')==1){
                      this.$router.push('ask')
                    }else {
                      this.$router.push('askStatement')
                    }
                }else {
                  this.loginLoading=false
                  this.$notify.error({
                    title: '',
                    message: res.data.message
                  });
                }
              }).catch(e=>{
                this.loginLoading=false
                this.$notify.error({
                  title: '',
                  message: ''
                });
              })

            } else {
              return false;
            }
          });
        },
        //
        handlerReg(){
          this.$refs['ruleReg'].validate((valid) => {
            if (valid) {
              if(this.regForm.radio==='1'){
                this.$ajax.post(regisUrl,{
                  mobile:this.regForm.mobile,
                  password:this.regForm.pass2,
                  yzm:this.regForm.code,
                  type:'pc'
                }).then((res)=>{

                    if(res.data.state){
                      this.regShow=false
                      this.loginShow=true
                      this.$notify.success({
                        title: '',
                        message: ''
                      });
                    }else {
                      this.$notify.error({
                        title: '',
                        message: res.data.message
                      });
                    }
                }).catch(e=>{
                  this.$notify.error({
                    title: '',
                    message: ''
                  });
                })

              }else {
                this.$notify.error({
                  title: '',
                  message: ''
                });
              }

            } else {

              return false;
            }
          });
        },
        // 
        handlerForgot(){

          this.$refs['ruleForgot'].validate((valid) => {
            if (valid) {
              //todo pass&repass  
              alert('submit!');
            } else {

              return false;
            }
          });
        }
      },
      watch:{
        ['regForm.radio'](val){

          if(val==='1'){
            this.agreementShow=true,
            this.regShow=false
          }
        }
      }
    }
</script>

<style scoped lang="less">
  @import url('../../../common/css/scratch_1');

.container{
  display: flex;
  justify-content: center;
  align-items: center;
  h3{
    font-size: 32px;
    color: @title;
    font-weight: bold;
    text-align: center;
    height: 104px;
    line-height: 104px;
  }

  &-box{
    background-color: #fff;
    border: 1px solid @boderColor;
    margin-top: 40px;
    width: 640px;
    height: 480px;
    span{
      color: @title;
      cursor: pointer;
    }
    &-forget{
      width: 500px;
      margin: 0 auto 10px auto;
      direction: rtl;
    }
    &-reg-text{
      margin-top: 10px;
      text-align: center;
    }
  }

  &-reg{
    background-color: #fff;
    width: 640px;
    margin-top: 50px;
    border: 1px solid @boderColor;
    span{
      color: @title;
      cursor: pointer;
    }
    &-info{
      display: flex;
      justify-content: space-between;
    }
  }

  &-agreement{
    margin-top: 20px;
    width: 1200px;
    background-color: #fff;
    border: 1px solid @boderColor;
    display: flex;
    justify-content: center;
    position: relative;
    >span{
      cursor: pointer;
      font-size: 30px;
      position: absolute;
      top:20px;
      right: 20px;
      font-weight: bolder;
    }
    &-box{
      width: 880px;
      h3{
        color: #2D3557;
        font-size: 30px;
        text-align: left;
        height: 160px;
        line-height: 160px;
        border-bottom: 1px solid #F8F8F8;
      }
      >div{
        margin-top: 20px;
        font-size: 16px;
        color: #2D3557;
        p{
          margin: 25px auto;
        }
      }
    }
  }
}

</style>



